html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
}

body {
  font-family: var(--font-body);
  color: var(--black);
  line-height: 1.4;
  background-color: var(--white);
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.wrapper {
  width: min(100% - 2.5rem, 75rem);
  margin-inline: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-header);
  line-height: 1.1;
  margin-top: 0;
}

h1 {
  font-size: 1.875rem;
  font-size: clamp(1.875rem, 0.98rem + 3.35vw, 3.5rem);
}

a,
a:visited,
a:active,
a:hover {
  text-decoration: none;
}

a[aria-current=true] {
  font-weight: 700;
  text-decoration: underline;
}

:root {
  --darkGreen: hsl(167, 97%, 15%);
  --nwcGreen: hsl(166, 100%, 20%);
  --lightGreen: hsl(109, 61%, 43%);
  --northernBlue: hsl(208, 100%, 33.5%);
  --lightBlue: hsl(214, 58%, 55%);
  --orange: hsl(31, 100%, 50%);
  --red: hsl(347, 100%, 45.3%);
  --yellow: hsl(49, 100%, 50%);
  --white: hsl(240, 100%, 99%);
  --black: hsl(40, 8%, 15%);
}

:root {
  --font-header: "Roboto", sans-serif;
  --font-body: "Libre Baskerville", serif;
}

.card {
  padding: 1.25rem;
}